import { Component, OnInit, OnDestroy, AfterViewInit, ViewChild, ElementRef } from '@angular/core';
import {Router, ActivatedRoute, Params} from '@angular/router';
import { CategoryService } from "../category.service";

import { environment } from '../../../environments/environment';
import { BaseForm } from '../../shared/base-form';
import { ToastrService } from 'ngx-toastr';

@Component({
  	selector: 'app-form',
  	providers: [],
  	templateUrl: './form.component.html',
  	styleUrls: ['./form.component.css']
})

export class FormComponent extends BaseForm  implements OnInit {
    public language;
  	constructor(
	    private router: Router,
	    private activatedRoute: ActivatedRoute,
	    private klassService: CategoryService,
	    private toastr: ToastrService
  	) { super(); }

  	ngOnInit() {
	    this.item.is_active = true ;
	    
	    this.language = 'en';
	    // subscribe to router event
	    this.activatedRoute.params.subscribe((params: Params) => {
	        this.itemID = params['id'];
	        if(this.itemID){
	          this.isEditable = true;
	          this.getItem();
	        }
	    });
	
    }

  	getItem() {
     	this.busy = this.klassService.getItem(this.itemID, this.language).then((res) => {
      		if(!res)
        		return true;

      		this.item = res.data;
      });
  	}

  	createItem() {
    	this.busy = this.klassService.addItem(this.item, this.language, 'categories').then((res) => {
      		if(!res)
        		return true;
          this.toastr.success('.',this.constantMessages.categoryCreated, { timeOut: 3000, });
          this.item = res;
      		this.router.navigate(['/category']);
    	});
  	}


  	updateItem() {
	    this.busy = this.klassService.updateItem(this.itemID, this.item, this.language, 'categories').then((res) => {
	      	if(!res)
	        	return true;
          this.toastr.success('.','Category Updated', { timeOut: 3000, });
          this.router.navigate(['/category']);
	    });
  	}
}


