import { Component, OnInit } from '@angular/core';

import { Listing } from '../../shared/listing';

import { CategoryService } from "../category.service";

@Component({
  selector: 'app-listing',
  templateUrl: './listing.component.html',
  styleUrls: ['./listing.component.css']
})
export class ListingComponent extends Listing implements OnInit {
  language: any;
  imagePath: any;
  public sortBy   = "name";
  public sortOrder = "asc";
  constructor(
    private klassService: CategoryService ) { super(); }

  ngOnInit() {
    this.language = 'en';
    this.getItems();
  }

  getItems() {
    this.items = [];
    this.busy = this.klassService.getItems(this.language, 'categories').then((res) => {
      if(!res)
        return true;
        this.items = res.data;
        this.tmpItems = this.items;
        this.itemsTotal = this.items.length;
    });
  }

  public filterItems() {
    if (this.tmpItems.length == 0)
      this.tmpItems = this.items;
    this.items = this.tmpItems.filter(_item =>
       _item.name.toLowerCase().lastIndexOf(this.searchTerm.trim().toLowerCase()) != -1 || 
       _item.description.toLowerCase().lastIndexOf(this.searchTerm.trim().toLowerCase()) != -1
    );
  }

  removeItem() {
    this.closeModal();
    this.item.is_deleted = true;
    this.busy = this.klassService.removeItem(this.item, this.language, 'categories').then((res) => {
      if(!res)
        return true;
      this.item = null;
      this.getItems();
    });
  }

  toggleStatus(_item) {
    this.busy = this.klassService.toggleStatus(_item, this.language, 'categories').then((res) => {
      if(!res)
        return true;
    });
  }
}


